:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --dkblack: hsl(0, 0%, 0%);
  --black: hsl(0, 0%, 11%);
  --ltblack: hsl(0, 0%, 21%);
  --white: hsl(0, 0%, 100%);
  --dkwhite: hsl(0, 8%, 71%);
  --gray: hsl(0, 0%, 49%);
  --yellow: hsl(60, 100%, 50%);
  --blue: hsl(240, 100%, 50%);
  --red: hsl(0, 100%, 50%);
  --orange: hsl(39, 100%, 50%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 150vh;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-weight: bolder;
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:hover {
  text-decoration: none;
  color: var(--white);
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

.header {
  display: flex;
  justify-content: center;
  margin-top: 0.9375rem;
}
.header__button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 210px;
  padding: 0.375rem;
  border: 0 solid;
  background-color: var(--gray);
  border-radius: 1.5rem;
  opacity: 0.85;
}
.header__button a {
  background-color: var(--black);
  color: var(--white);
  padding: 6px 26px;
  margin: 0.0625rem 0.06875rem;
  cursor: pointer;
  border-radius: 1rem;
  width: 6.25rem;
  height: 2.1875rem;
  box-shadow: 0 0.625rem 1.5625rem rgba(0, 0, 0, 0.5);
  transition: opacity 200ms ease-in-out;
}
.header__button a:hover {
  background-color: var(--ltblack);
}
.header__button:hover {
  opacity: 0.82;
}

.no-scroll {
  position: fixed;
}

body.menu-open .sliders {
  display: none;
}

.topnav {
  display: flex;
  justify-content: space-between; /* Align items to the right */
  align-items: center;
  padding-block: 1.5rem;
}
.topnav button {
  background: none;
  border: none;
  padding: 0;
}
.topnav__blockm {
  position: absolute;
  right: 2rem;
  width: 5.9375rem;
  height: 3.9375rem;
  border-radius: 2.25rem;
  background-color: var(--dkblack);
  cursor: pointer;
}
@media (max-width: 30em) {
  .topnav__blockm {
    border-radius: 1.25rem;
    right: 1rem;
    width: 3.938rem;
    height: 3.938rem;
  }
}
.topnav__open[aria-expanded=true] + .topnav__menu {
  translate: 0;
  opacity: 1;
}
.topnav__close {
  display: block;
  margin-inline-start: auto;
  margin-block-end: 8rem;
  cursor: pointer;
  background-color: var(--white);
}
@media (max-width: 28.875em) {
  .topnav__close {
    position: relative;
    right: -11px;
  }
}
.topnav__close img {
  width: 2.5rem;
}
.topnav__menu {
  position: fixed;
  inset: 0;
  padding: 1.5rem;
  background-color: var(--dkblack);
  translate: 100vw 0;
  transition: translate 0.5s ease-in-out;
  translate: 0;
  opacity: 0;
  transition: opacity 250ms ease-in-out;
}
.topnav__links {
  display: flex;
  align-items: center;
  margin: 0;
  flex-direction: column;
}
.topnav__item {
  list-style-type: none;
  position: relative;
}
.topnav__item .topnav__link {
  position: relative;
  display: inline-block;
  background-color: #444; /* Box background color */
  color: white; /* Text color */
  padding: 0.5rem 1rem; /* Adjust padding as needed */
  border-radius: 0.25rem; /* Adjust border radius as needed */
  cursor: pointer; /* Change cursor on hover */
}
.topnav__item .topnav__link:hover {
  background-color: #666; /* Change background color on hover */
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  background-color: var(--dkwhite);
  border-radius: 2rem;
  margin-top: 6rem;
  width: 41.75rem;
}
@media (max-width: 43em) {
  .cards {
    width: 20.75rem;
  }
}
.cards__title {
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: bolder;
}
.cards p {
  font-size: 1.25rem;
  line-height: 1.5;
  font-weight: bold;
}
.cards:hover {
  background-color: var(--orange);
}

.card2 {
  position: relative;
  top: 215px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.center {
  display: flex;
  text-align: center;
  position: relative;
  perspective: 1200px;
  width: 748px;
  left: -214px;
}
@media (min-width: 100em) {
  .center {
    left: -100px;
  }
}
@media (min-width: 50em) {
  .center {
    left: -143px;
    bottom: -103px;
  }
}
@media (max-width: 35em) {
  .center {
    justify-content: center;
    width: 389px;
    text-align-last: center;
  }
}

.center .img-card {
  position: relative;
  height: 350px;
  width: 400px;
  transform-style: preserve-3d;
  transition: transform 0.5s ease-in;
}

.img-card img {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.img-card img:nth-child(1) {
  transform: rotateX(-270deg) translateY(-175px);
  transform-origin: top left;
}

.img-card img:nth-child(2) {
  transform: translateZ(175px);
}

.img-card img:nth-child(3) {
  transform: rotateX(-90deg) translateY(175px);
  transform-origin: bottom center;
}

.img-card img:nth-child(4) {
  transform: rotateX(-180deg) translateY(350px) translateZ(175px);
  transform-origin: bottom right;
}

#tab-1:checked ~ .img-card {
  transform: rotateX(-90deg);
}

#tab-2:checked ~ .img-card {
  transform: rotateX(0deg);
}

#tab-3:checked ~ .img-card {
  transform: rotateX(90deg);
}

#tab-4:checked ~ .img-card {
  transform: rotateX(180deg);
}

.sliders {
  position: absolute;
  display: block;
  right: 0;
  top: -30px;
}
@media (max-width: 40em) {
  .sliders {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
    top: 388px;
    right: 0;
    left: 81%;
    transform: translateX(-50%);
  }
}

.sliders label {
  height: 98px;
  width: 110px;
  display: flex;
  overflow: hidden;
  cursor: pointer;
  margin: 6px 0;
  border: 2px solid #2d4052;
}

.sliders label:nth-child(1) {
  margin-top: 0;
}

label img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: grayscale(1);
  opacity: 0.9;
}

label img:hover #tab-1:checked ~ .sliders label:nth-child(1) img,
#tab-2:checked ~ .sliders label:nth-child(2) img,
#tab-3:checked ~ .sliders label:nth-child(3) img,
#tab-4:checked ~ .sliders label:nth-child(4) img {
  filter: grayscale(0);
  opacity: 1;
}

input {
  display: none;
}